<template>
    <div class='addPromotion'>
        <div class='top'>
           <span class='fundInfo' @click='goback()'>运营推广</span>
           <img src='../../assets/images/icon_nav_arrow.png'/>
           <span class='branch'>添加活动</span>
        </div>
        <el-form ref="form" :model="form" :rules='rules'>
            <div class='content'>
                <div class='left'>
                    <el-form-item label="优惠券类型：" class='sel'>
                        <el-input disabled value='免单券'></el-input>
                    </el-form-item>
                    <el-form-item label="活动课程类型：" class='sel'  prop='courseType'>
                        <el-select v-model="form.courseType " placeholder="请选择" @change="getCourse($event)">
                            <el-option label="团课" value="GROUP"></el-option>
                            <el-option label="私教课" value="PRIVATE"></el-option>
                            <el-option label="特色课" value="SPECIAL"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="typeList" class='check bac' v-if='courseList.length > 0'>
                        <el-checkbox-group v-model="form.typeList">
                            <el-checkbox v-for="(item, index) in courseList" :key="index" :label="item.id" >{{item.name}}</el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="优惠券名称：" class='bac sel' prop='name'>
                        <el-input v-model="form.name" :maxlength="15" placeholder="请输入"></el-input>
                    </el-form-item>
                    <el-form-item label="每人领取张数：" class='sel bac' prop='couponNumber'>
                        <el-input v-model.number="form.couponNumber" :maxlength="2"></el-input>
                    </el-form-item>
                    <el-form-item label="有效期至：" class='time bac newTime'>
                        <el-date-picker type="datetime" placeholder="请选择日期" v-model="form.couponEndTime" :picker-options="pickerOptions1"></el-date-picker>
                    </el-form-item>
                    <div class='tips'>注：（不填则有效期为"无限期"）</div>
                    <el-form-item label="活动开始时间：" class='sel bac newTime' prop='activityStartTime'>
                        <el-date-picker type="datetime" placeholder="请选择日期" v-model="form.activityStartTime" :picker-options="pickerOptions1"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="活动结束时间：" class='sel bac newTime' prop='activityEndTime'>
                        <el-date-picker type="datetime" placeholder="请选择日期" v-model="form.activityEndTime" :picker-options="pickerOptions1"></el-date-picker>
                    </el-form-item>
                </div>
                <div class='cen'>
                    <el-form-item label="通知教练：" class='bac radio'>
                        <el-radio-group v-model="form.notification">
                            <el-radio label="0">通知</el-radio>
                            <el-radio label="1">不通知</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="通知文案：" class='bac'>
                        <el-input v-model='form.notificationContent' value='' :maxlength="12"></el-input>
                    </el-form-item>
                    <el-form-item label="备注：" class='textArea bac'>
                        <el-input type="textarea" v-model="form.note" placeholder="请输入" :maxlength="300"></el-input>
                    </el-form-item>
                </div>
                <div class='rig'></div>
            </div>
            <el-form-item class='btn'>
                <el-button @click="goback">取消</el-button>
                <el-button class='submit' @click="onSubmit('form')">确定</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import {addFreeCard, getallprivate, queryspecial, getgroupsimple, updateFreeCard} from '@/services/service'
import {isApiResponseOk, getSessionStorage} from '@/utils/com'
export default {
    data () {
        return {
            form: {
                name: '',
                region: '',
                activityEndTime: '',
                activityStartTime: '',
                couponEndTime: '',
                couponNumber: '',
                couponType: 'DEDUCTION',
                typeList: [],
                note: '',
                courseType: '',
                specialCourseList: [],
                privateCourseList: [],
                groupCourseList: [],
                notification: '',
                notificationContent: ''
            },
            rules: {
                courseType: [
                    { required: true, message: '请选择课程类型', trigger: 'change' }
                ],
                couponNumber: [
                    { required: true, message: '请输入赠书张数', trigger: 'blur' },
                    { type: 'number', message: '必须为数字值'}
                ],
                activityStartTime: [
                    { required: true, message: '开始时间不能为空', trigger: 'change' }
                ],
                activityEndTime: [
                    { required: true, message: '结束时间不能为空', trigger: 'change' }
                ],
                typeList: [
                    { required: true, message: '课程不能为空', trigger: 'change' }
                ],
                name: [
                    { required: true, message: '请输入名称', trigger: 'blur' }
                ]
            },
            courseList: [],
            pickerOptions1: {
                disabledDate(time) {
                return time.getTime() < new Date(new Date().toLocaleDateString()).getTime();
                }
            },
            radiolist: [0,1],
            modify: false
        }
    },
    created () {
        this.modify = this.$route.query.modify
        if (this.modify) {
            let data = getSessionStorage('freeDetail')
            let name = data.couponName
            let courseType = ''
            let typeList = []
            data.coursePOList.filter((item) => {
                typeList.push(parseInt(item.courseId))
                courseType = item.courseType
            })
            this.getCourse(courseType)
            delete data.coursePOList
            Object.assign(data, {courseType, typeList, name})
            this.form = data
            this.form.notification = String(this.form.notification)
        }
    },
    methods: {
        async onSubmit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.form.activityEndTime = this.getTime(this.form.activityEndTime)
                    this.form.activityStartTime = this.getTime(this.form.activityStartTime)
                    this.form.couponEndTime = this.getTime(this.form.couponEndTime)
                    if (this.modify) {
                        this.updateFreeCard(this.form)
                    } else {
                        this.addFreeCard(this.form)
                    }
                } else {

                }
            })
        },
        goback () {
            this.$router.go(-1);
        },
        // 获取课程类型
        getCourse (data) {
            if (data === 'GROUP') {
                // 团课
                this.groupClass()
            } else if (data === 'PRIVATE') {
                // 私教课]
                this.privateClass()
            } else if (data === 'SPECIAL') {
                // 特色课
                this.specalClass()
            }
        },
        async groupClass () {
            const res = await getgroupsimple ()
            if (isApiResponseOk(res)){
                this.courseList = res.data
            }
        },
        async privateClass () {
            const res = await getallprivate ()
            if (isApiResponseOk(res)){
                this.courseList = res.data
            }
        },
        async specalClass () {
            const res = await queryspecial ()
            if (isApiResponseOk(res)){
                this.courseList = res.data
            }
        },
        async addFreeCard(data) {
            if (data.courseType === 'GROUP') {
                data.groupCourseList = data.typeList
            } else if (data.courseType === 'PRIVATE') {
                data.privateCourseList = data.typeList
            } else if (data.courseType === 'SPECIAL') {
                data.specialCourseList = data.typeList
            }
            const res = await addFreeCard(data)
            if(isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '添加成功',
                    type: 'success'
                })
                this.goback()
            }
        },
        getTime (str) {
            if (!str) return
            var date = new Date(str)
            return date.getTime()
        },
        async updateFreeCard(data) {
            if (data.courseType === 'GROUP') {
                data.groupCourseList = data.typeList
            } else if (data.courseType === 'PRIVATE') {
                data.privateCourseList = data.typeList
            } else if (data.courseType === 'SPECIAL') {
                data.specialCourseList = data.typeList
            }
            const res = await updateFreeCard(data)
            if(isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '修改成功',
                    type: 'success'
                })
                this.goback()
            }
        }
    }
}
</script>
<style lang="scss" scope>
.el-button--text{
    color: #20c6ba !important;
}
</style>





